@import '../variables';

.header {
  padding: 40px 0;

  @media (max-width: 1260px) {
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    width: 70px;
    height: 80px;

    img {
      margin-right: 20px;
    }

    h1 {
      position: absolute;
      font-weight: 900;
      font-size: 24px;

      padding-top: 20px;
    }
  }

  &__menu {
    padding-left: 220px;
    li {
      font-weight: 600;
      font-size: 20px;
      display: inline-block;
      padding-left: 35px;
      cursor: pointer;
      text-decoration: none;
      line-height: 1;

      &:after {
        display: block;
        left: 0;
        width: 0%;
        height: 2px;
        background-color: rgb(0, 0, 0);
        content: '';
        transition: width 0.3s ease-in-out;
      }
      &:hover::after,
      &:focus::after {
        width: 100%;
      }
    }
  }

  &__log {
    li {
      font-weight: 600;
      font-size: 20px;
      display: inline-block;
      padding-left: 35px;
      cursor: pointer;
      text-decoration: none;
      line-height: 1;

      &:after {
        display: block;
        left: 0;
        width: 0%;
        height: 2px;
        background-color: rgb(0, 0, 0);
        content: '';
        transition: width 0.3s ease-in-out;
      }
      &:hover::after,
      &:focus::after {
        width: 100%;
      }
    }
  }
}
