.sizes-block {
}

.sizes {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;

  li {
    padding: 5px;
    border: 1px solid rgb(255, 208, 147);
    color: white;
    border-radius: 50%;
    cursor: pointer;

    &.active {
      color: red;
      background-color: #fff;
    }
  }
}

