@import '../variables';

.sale-block {
  width: 100%;
  margin-bottom: 50px;
  background: #e5e5e5;
  border-radius: 59px;
  height: 558px;
}

.container__sale {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 33px;
  // visibility: hidden;

  &--botton {
    width: 150px;
    height: 53px;
    background: #ccc9c9;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 33px;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 24px;
    }
  }
}
