.product {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  // justify-content: space-between;
  align-items: center;
  margin-left: 15%;
  margin-top: -170px;

  &__menu {
    position: relative;
    display: flex;
    padding-bottom: 61px;

    &--card {
      width: 271px;
      height: 433px;
      background: #ffb524;
      border-radius: 27px;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    }

    &--imgblock {
      margin: 24px 19px;
      width: 232px;
      height: 173px;
      background: #fff;
      border-radius: 17px;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &--img {
      width: 155px;
      height: 155px;
    }

    &--descrip {
      display: inline-block;
      justify-content: space-between;
      align-items: center;
      margin-left: 18px;
    }
    &--title {
      font-weight: 800;
      font-size: 16px;
      line-height: 20px;
      min-height: 40px;
    }

    &--subtitle {
      width: 227px;
      min-height: 80px;
      height: 63px;

      padding-top: 12px;
      margin-bottom: 18px;
      font-weight: 400;
      font-size: 13px;
      line-height: 16px;
    }

    &--basket {
      position: absolute;
      bottom: 10px;
      padding-top: 20px;
    }

    &--weight {
      display: flex;
      font-weight: 600;
      font-size: 13px;
      line-height: 16px;

      p {
        padding-left: 3px;
      }
    }

    &--quant {
      padding-left: 45px;
      font-weight: 600;
      font-size: 13px;
      line-height: 16px;
    }

    &--price {
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      padding-bottom: 8px;
    }

    &--butt {
      background: none;
      border: none;
      width: 54px;
      height: 54px;

      position: relative;
      margin-left: 179px;
      bottom: 55px;
      cursor: pointer;
      border-radius: 50%;

      &:hover {
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transform: translateY(-3px);
      }

      &:active {
        transform: translateY(-1px);
      }
    }
  }
}
