@import './variables';
@import './libs/normalize';

@import 'components/all';

body {
  background-color: $background;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 150px;
  background: url(./sushibag.png) no-repeat;
  border-radius: 0px 0px 35px 35px;

  margin: auto;
  max-width: 1400px;

  @meida (max-width: 645px) {
    width: calc(100vw - 30px);
    margin-top: 15px;
  }
}

.content {
  padding: 40px 0;

  h2 {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  // p {
  //   font-size: 20px;
  // }
}

.container {
  width: $container-width;
  margin: 0 auto;
}
