.swiper__product-button-prev,
.swiper__product-button-next {
  bottom: 195px;
  position: relative;
  font-size: 50px;
  border: none;

  // margin-top: calc(0px - (var(--swiper-navigation-size) / 2));
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;

  &:disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
  &.swiper__product-button-hidden {
    opacity: 0;
    cursor: auto;
    pointer-events: none;
  }
  .swiper__product-navigation-disabled & {
    display: none !important;
  }
  &:after {
    font-family: swiper-icons;
    font-size: var(--swiper-navigation-size);
    text-transform: none !important;
    letter-spacing: 0;
    font-variant: initial;
    line-height: 1;
  }
}
.swiper__product-button-prev,
.swiper__product-rtl .swiper__product-button-next {
  &:after {
    content: 'prev';
  }
  bottom: 131px;
  left: 30px;
  right: auto;
  background: #ccc9c9;
  border-radius: 100%;
  width: 64px;
  height: 64px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.swiper__product-button-next,
.swiper__product-rtl .swiper__product-button-prev {
  &:after {
    content: 'next';
  }
  // right: 20px;
  left: 92%;
  background: #ccc9c9;
  border-radius: 100%;
  width: 64px;
  height: 64px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.swiper__product-button-lock {
  display: none;
}

.swiper__product {
  // top: 0px;
  padding-bottom: 43px;

  margin-left: auto;
  margin-right: auto;
}

.productSlider {
  top: 33px;
  width: 80%;
  height: 200px;

  &__content {
    padding-top: 5px;
    padding-left: 12px;
    right: 11px;
  }

  &__content-row {
    padding-top: 5px;
    padding-left: 12px;

    &--img {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 98px;
      height: 98px;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      &-ico {
        width: 57px;
        height: 57px;
      }
    }

    &--img:hover {
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    }
    &--img:active {
      transform: translateY(-1px);
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    }

    &--title {
      text-align: center;
      padding-top: 13px;
      padding-right: 15px;
      padding-bottom: 5px;
      font-weight: 900;
      font-size: 22px;
      line-height: 20px;
      color: #000000;
    }
  }
}

.productSlider__content-row .productSlider__content-row--title {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

.productSlider__content-row:hover .productSlider__content-row--title {
  visibility: visible;
  opacity: 1;
}

.productSlider__content-row:hover {
  cursor: pointer;
  transform: translateY(-3px);
}

.productSlider__content-row:active {
  transform: translateY(-1px);
}

.productSlider__content-row.active .productSlider__content-row--title {
  visibility: visible;
  opacity: 1;
}

