@import '../variables';

.product-block {
  width: 100%;
  margin: 0 auto;
  background: #e5e5e5;
  border-radius: 59px;
  height: 100%;
  // height: 1421px;
}

.container__product {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 33px;

  &--botton {
    width: 191px;
    height: 53px;
    background: #ccc9c9;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 33px;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 24px;
    }
  }
}
