.swiper__sale {
  top: 33px;
  padding-bottom: 43px;

  margin-left: auto;
  margin-right: auto;
}

.swiper__sale-button-prev,
.swiper__sale-button-next {
  bottom: 230px;
  position: relative;
  font-size: 50px;
  border: none;

  margin-top: calc(0px - (var(--swiper-navigation-size) / 2));
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;

  &:disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
  &.swiper__sale-button-hidden {
    opacity: 0;
    cursor: auto;
    pointer-events: none;
  }
  .swiper__sale-navigation-disabled & {
    display: none !important;
  }
  &:after {
    font-family: swiper-icons;
    font-size: var(--swiper-navigation-size);
    text-transform: none !important;
    letter-spacing: 0;
    font-variant: initial;
    line-height: 1;
  }
}
.swiper__sale-button-prev,
.swiper__sale-rtl .swiper__sale-button-next {
  &:after {
    content: 'prev';
  }
  bottom: 166px;
  left: 30px;
  right: auto;
  background: #ccc9c9;
  border-radius: 100%;
  width: 64px;
  height: 64px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.swiper__sale-button-next,
.swiper__sale-rtl .swiper__sale-button-prev {
  &:after {
    content: 'next';
  }
  // right: 20px;
  left: 92%;
  background: #ccc9c9;
  border-radius: 100%;
  width: 64px;
  height: 64px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.swiper__sale-button-lock {
  display: none;
}

.saleSlider__content {
  // text-align: center;
  display: flex;
  justify-content: center;
  padding: 24px;
  left: 1px;
  right: 1px;
}

.saleSlider {
  top: 33px;
  width: 70%;
  height: 369px;

  &__content {
    background: #ffa552;
    border-radius: 27px;

    // display: flex;
    // align-items: center;
    // justify-content: space-between;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  &__content-row {
    &--img {
      display: flex;
      align-items: center;
      justify-content: center;
      background: #fff;
      border-radius: 17px;
      width: 232px;
      height: 173px;
      overflow: hidden;

      img {
        // padding-top: 5px;
        width: 155px;
        height: 155px;
        // object-fit: fill;
      }
    }

    h1 {
      padding-top: 13px;
      padding-bottom: 5px;
      font-weight: 900;
      font-size: 16px;
      line-height: 20px;
      color: #000000;
    }

    &--descrip {
      font-weight: 400;
      font-size: 13px;
      line-height: 16px;
      color: #000000;
      margin-bottom: 18px;
    }

    // &--sizes {
    //   display: flex;
    //   justify-content: center;
    //   column-gap: 10px;
    // }
    &--size {
      // padding: 5px;
      // border: 1px solid rgb(255, 208, 147);
      // color: white;
      // border-radius: 50%;
      // // cursor: pointer;

      &.active {
        color: red;
        background-color: #fff;
      }
    }

    &--weight {
      padding-top: 11px;
      font-weight: 500;
      font-size: 13px;
      line-height: 16px;

      color: #000000;
    }

    &--price {
      // padding-top: 5px;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
    }

    &--basket {
      background: none;
      border: none;
      width: 54px;
      height: 54px;

      position: relative;
      margin-left: 179px;
      top: -45px;
      cursor: pointer;
      border-radius: 50%;

      &:hover {
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transform: translateY(-3px);
      }

      &:active {
        transform: translateY(-1px);
      }
    }
  }
}
